"use client";

import { UploadForm } from "@/components/UploadForm";

export default function AdminResourcesPage() {
    return (
        <div className="space-y-6">
            <div>
                <h1 className="text-3xl font-bold tracking-tight">Resources</h1>
                <p className="text-muted-foreground">Upload and manage study materials.</p>
            </div>

            <div className="grid gap-8 lg:grid-cols-3">
                <div className="lg:col-span-2">
                    <UploadForm />
                </div>

                {/* Future: Resource List/Search could go here */}
                <div className="rounded-xl border border-zinc-200 bg-white p-6 shadow-sm dark:border-zinc-800 dark:bg-zinc-900">
                    <h3 className="mb-4 text-lg font-semibold">Guidelines</h3>
                    <p className="text-sm text-muted-foreground">
                        Please ensure all resources are properly categorized by branch and year.
                        PDFs are preferred for notes and question papers.
                    </p>
                </div>
            </div>
        </div>
    );
}
